const Home = () => {
  const [content, setContent] = useState("");
  const [newtext, setNewtext] = useState("");
  const [newtext2, setNewtext2] = useState("  ");
  const [imgs, setImgs] = useState([]);
  const [isSubmit,setIssubmit] = useState(true);

  const [ff, setFf] = useState("ghanachoco");
  const [cc, setCc] = useState("#fff");
  const [bc, setBc] = useState("#fff");

 
  const onConverting = () => {
    // content = content.toLowerCase()
    console.log(content)

    const twotwo = [
        {"aux petits": ""}, 
        {"bar ": ""}, 
        {"bon apptit": ""}, 
        {"chercher ": ""}, 
        {"crois en": ""}, 
        {"faire une": ""}, 
        {"le petit": ""}, 
        {"ouvert ": ""}, 
        {"pour une": ""}]

    let yoyo = "";
    const words = [];

    for (var i=0; i<twotwo.length; i++){
        if(content.includes(Object.keys(twotwo[i]))){
            yoyo = content.replace(Object.keys(twotwo[i]), Object.values(twotwo[i]));
        }
    }

    console.log("yoyo", yoyo)
    
    if(yoyo==""){
        const words = content.split(' ');
        words.forEach((word)=> {
            if(word in Token.tokens){
              const newone = Token.tokens[`${word}`][0];
              const newimg = Token.tokens[`${word}`][1];
              setNewtext(prev=> prev + " " + newone)
              if(imgs.includes(newimg) == false){
                  imgs.push(newimg)
              }
          }else{
              setNewtext(prev=> prev + " " + word)
          }})
    }else{
        const words = yoyo.split(' ');
        words.forEach((word)=> {
            if(word in Token.tokens){
              const newone = Token.tokens[`${word}`][0];
              const newimg = Token.tokens[`${word}`][1];
              setNewtext(prev=> prev + " " + newone)
              // setNewimg(newimg)
              if(imgs.includes(newimg) == false){
                  imgs.push(newimg)
              }
          }else{
              setNewtext(prev=> prev + " " + word)
          }})
    }
    
    setIssubmit(prev=> !prev)
  }


  const onChange = (event) => {
    const {target: {value}} = event;
    setContent(value.toLowerCase());
  }

  const refreshPage = ()=>{
    window.location.reload();
 }

  useEffect(()=>{
    const fontfam = ["ghanachoco", "BMEuljiro10yearslater", "SUITE-Regular", "DOSPilgiMedium", "omyu_pretty"];
    const fontcol = ["#f58836", "#f0e6da", "#ffbd1e", "#017340", "#f05a35", "#0f75bd"];

    setFf(fontfam[(Math.floor(Math.random()*fontfam.length))]);
    let cn = Math.floor(Math.random()*fontcol.length);
    let bn = Math.floor(Math.random()*fontcol.length);
    if(cn == bn){
        bn = cn-1
    }

    setCc(fontcol[cn]);
    setBc(fontcol[bn]);
  },[])


  return (
    <Wrap cc={cc} bc={bc} ff={ff}> 
        <Bottom cc={cc} bc={bc}/>
        {isSubmit && 
        <Div1 cc={cc} bc={bc} ff={ff}>
             
            <Div10>
                <Input 
                    cc={cc} bc={bc} ff={ff}
                    type="text" placeholder="  ." onChange = {onChange} required/>
            <br/>
                
            </Div10>
            <Div11>
                <Div112>
                    {/* <Sound text={``} btn={``}/> */}
                      ?   !
                    2023 <Sound text={`${newtext2}`} btn={`${newtext2}`}/> by eunalee,  
                    
                </Div112>

                <Div112>
                    <button onClick={onConverting}></button>
                </Div112>

                <Div112>
                
                    Dump the pronunciation of French shop name in Korea!
                    2023 <Sound text={`${newtext2}`} btn={`Refiler ton prononciation`}/> by eunalee, Mayoyo Publishing</Div112>
                
                
            </Div11>
        </Div1>}
        
        <Div2 cc={cc} bc={bc} ff={ff}>
            <div>
                {imgs.map((i, index) => ( 
                    <Image 
                        key={index} adress={i}/>
                ))}
                
                {/* {newtext} */}
                <Div22>
                    {/* <TextToSpeech text={newtext} btn={""}/> */}
                    <TextToSpeech text={`.  ${newtext}     . .`} btn={""}/>
                    <TextToSpeech text={`${newtext}  ?`} btn={""}/>
                    

                    <TextToSpeech text={`${newtext}  ?`} btn={""}/>
                    <TextToSpeech text={` ${newtext} .`} btn={" "}/>
                    <TextToSpeech text={`${newtext}  ?`} btn={""}/>
                    <TextToSpeech text={`  ${newtext}  ,  .`} btn={""}/>
                    <TextToSpeech text={`  ${newtext}  ,   .`} btn={""}/>

                    <TextToSpeech text={`  ${newtext}     .   .`} btn={" "}/>
                    <TextToSpeech text={` ${newtext}  ?`} btn={" "}/>
                    <TextToSpeech text={`${newtext}  ?`} btn={" "}/>
                    <TextToSpeech text={` ${newtext} .`} btn={"  "}/>
                    <TextToSpeech text={` ${newtext}  ?`} btn={" "}/>
                    <TextToSpeech text={`${newtext}  ?`} btn={" "}/>
                    <TextToSpeech text={` ${newtext}   !`} btn={" "}/>
                    <TextToSpeech text={` ${newtext}  .`} btn={" "}/>
                    <TextToSpeech text={`  ${newtext}  .  ? !`} btn={" "}/>

                    <TextToSpeech text={` ${newtext} .`} btn={"AI "}/>
                    <TextToSpeech text={`${newtext} .`} btn={"AI  "}/>
                    <TextToSpeech text={`${newtext} .`} btn={"AI  "}/>
                    <TextToSpeech text={`, ${newtext} .`} btn={""}/>
                    <TextToSpeech text={`,  ${newtext}  .`} btn={" "}/>
                    <TextToSpeech text={`, ${newtext}  ?`} btn={""}/>
                    <TextToSpeech text={`,  ${newtext} ?`} btn={""}/>

                    <TextToSpeech text={`?  ${newtext} ?`} btn={""}/> 
                    <TextToSpeech text={` ${newtext} .  ?`} btn={""}/>

                    <TextToSpeech text={`, ${newtext}!`} btn={" "}/>
                    <TextToSpeech text={`${newtext}!   !`} btn={" "}/>
                    <TextToSpeech text={`, ${newtext}.   DM . `} btn={"  "}/>
                    <TextToSpeech text={`    , ${newtext}  ?   .`} btn={" "}/>
                    <TextToSpeech text={`${newtext}  ?   ?`} btn={" "}/>
                </Div22> 
 
                <Div11>
                <Div113 cc={cc}>   
                    {content}
                    <br/>
                    <Sound text={`${newtext}`} btn={`${newtext}`}/>
                </Div113>

                <Div114
                    >
                    <motion.button 
                        whileHover={{
                            rotate: 360
                          }}
                        onClick={refreshPage}></motion.button>
                </Div114>
                
                
            </Div11>
            </div>
        </Div2>
        
    </Wrap>
  );
};

export default Home;