import React, { useState, useEffect } from "react";
import {keyframes} from 'styled-components';
import { motion } from "framer-motion"

const Image = ({adress}) => {
  const [color, setColor] = useState("");
  const colorset = ["#f58836", "#f0e6da", "#ffbd1e", "#017340", "#f05a35", "#0f75bd"]

  useEffect(()=>{
    const num = Math.floor(Math.random()*colorset.length)
    setColor(colorset[num]);
    console.log(color); 
  },[])  

  return (
        <motion.img 
            drag
            dragElastic={0}
            dragMomentum={false}
            whileDrag={{scale: 1.25}}
            src={adress} 
            style={{
                zIndex: 3,
                position: "fixed",
                top:  Math.floor(Math.random()*(window.innerHeigh-100)),
                left:  Math.floor(Math.random()*window.innerWidth-100),
                borderRadius: "10vw",
                filter: `drop-shadow(15px 10px 5px ${color})`,
                cursor: "pointer",
               
            }}/>
  );
};

export default Image;